@use 'variables' as *;

.zwssgr-switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;

    input {
        opacity: 0;
        width: 0;
        height: 0;
    }
}

.zwssgr-toggle-slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--border-gray);
    transition: .4s;
    border-radius: 34px;

    &:before {
        position: absolute;
        content: "";
        height: 26px;
        width: 26px;
        left: 4px;
        bottom: 4px;
        background-color: var(--white-color);
        transition: .4s;
        border-radius: 50%;
    }
}

input:checked + .zwssgr-toggle-slider {
    background-color: var(--secondary-color);

    &:before {
        transform: translateX(26px);
    }
}
